export const Features = () => {
  const { t } = useTranslation();
  const featureData = getFeatureData(t);
  const [selectedFeature, setSelectedFeature] = useState(featureData[0]);
  const [isLoading, setIsLoading] = useState(false);
  const [isExpanded, setIsExpanded] = useState(false);
  const iframeRef = useRef<HTMLIFrameElement>(null);
  const expandedIframeRef = useRef<HTMLIFrameElement>(null);

  // Add escape key event listener
  useEffect(() => {
    const handleEscKey = (event: KeyboardEvent) => {
      if (event.key === 'Escape' && isExpanded) {
        setIsExpanded(false);
      }
    };

    window.addEventListener('keydown', handleEscKey);

    // Clean up event listener on component unmount
    return () => {
      window.removeEventListener('keydown', handleEscKey);
    };
  }, [isExpanded]);

  const openLiveDemo = () => {
    window.open('https://dorotaiszymon.weddie.site', '_blank', 'noopener,noreferrer');
  };

  const handleZoomToggle = () => {
    setIsExpanded(true);

    // Sync the expanded iframe with the current iframe
    if (iframeRef.current && expandedIframeRef.current) {
      expandedIframeRef.current.src = iframeRef.current.src;
    }
  };

  const handleCloseExpanded = () => {
    setIsExpanded(false);
  };

  // Keep track of which button was clicked for animation
  const [clickedButtonId, setClickedButtonId] = useState<string | null>(null);

  const handleFeatureClick = (feature: typeof featureData[0]) => {
    // Don't do anything if the same feature is clicked again
    if (selectedFeature.id === feature.id) return;

    // Set the clicked button for animation
    setClickedButtonId(feature.id);

    // Remove the clicked class after animation completes
    setTimeout(() => {
      setClickedButtonId(null);
    }, 1000);

    // Show loading indicator
    setIsLoading(true);

    // Update selected feature
    setSelectedFeature(feature);

    // Get iframe reference
    const iframe = iframeRef.current;
    if (iframe) {
      // Create URL with hash for section
      const newUrl = `${FEATURES_BASE_URL}/#${feature.sectionId}`;

      // Add animation class to screen content
      const screenContent = document.querySelector('.screen-content');
      if (screenContent) {
        screenContent.classList.add('screen-content-animating');

        // Remove animation class after it completes
        setTimeout(() => {
          screenContent.classList.remove('screen-content-animating');
        }, 800);
      }

      // Update iframe source
      iframe.src = newUrl;

      // Hide loading indicator after a delay
      setTimeout(() => {
        setIsLoading(false);
      }, 800);
    }
  }

  return (
    <div css={SectionContainerStyle} id={FEATURES_ID}>
      {/* Header section with title and description */}
      <Typography variant="h2" css={MainTitleStyle}>
        {t('features.title')}
      </Typography>

      <Typography
        css={[MainDescriptionStyle, HighlightStyle]}
        dangerouslySetInnerHTML={{
          __html: t('features.description')
            .replace('<highlight>', `<span class="highlight">`)
            .replace('</highlight>', '</span>')
        }}
      />

      {/* Main content in row layout */}
      <div css={ContentContainerStyle}>
        <div css={LeftColumnStyle}>
          <div css={LaptopContainerStyle}>

            <div css={ScreenContentStyle} className="screen-content">
              <iframe
                ref={iframeRef}
                src={FEATURES_BASE_URL}
                title="Wedding website preview"
                css={WebsiteIframeStyle}
                loading="lazy"
              />
              <IconButton
                css={ZoomButtonStyle}
                onClick={handleZoomToggle}
                aria-label="Zoom in"
              >
                <ZoomInIcon />
              </IconButton>
              {isLoading && (
                <div css={LoadingOverlayStyle}>
                  <CircularProgress size={40} />
                </div>
              )}
            </div>
            <img
              src={ImageResource.laptop}
              alt="Laptop display"
              css={LaptopImageStyle}
            />

            {/* Expanded view */}
            <Backdrop
              open={isExpanded} css={BackdropStyle} onClick={handleCloseExpanded}
              transitionDuration={350} 
            >
              <div css={ExpandedScreenStyle} onClick={(e) => e.stopPropagation()}>
                <div css={ExpandedScreenContentStyle}>
                  <iframe
                    ref={expandedIframeRef}
                    src={FEATURES_BASE_URL}
                    title="Wedding website preview expanded"
                    css={ExpandedWebsiteIframeStyle}
                    loading="lazy"
                  />
                </div>
                <div css={ExpandedControlsStyle}>
                  <IconButton onClick={handleCloseExpanded} aria-label="Close">
                    <CloseIcon />
                  </IconButton>
                </div>
              </div>
            </Backdrop>
          </div>
        </div>

        <div css={RightColumnStyle}>
          <div css={FeatureButtonsContainerStyle}>
            {featureData.map((feature) => (
              <Button
                key={feature.id}
                startIcon={feature.icon}
                css={FeatureButtonStyle}
                className={`
                  ${selectedFeature.id === feature.id ? 'active' : ''}
                  ${clickedButtonId === feature.id ? 'clicked' : ''}
                `}
                onClick={() => handleFeatureClick(feature)}
              >
                {feature.buttonName}
              </Button>
            ))}
          </div>

          <div css={FeatureContentStyle}>
            <Typography variant="h4" css={TitleStyle} style={{ fontSize: '2.2rem', marginBottom: '1rem' }}>
              {selectedFeature.title}
            </Typography>

            <Typography css={DescriptionStyle} dangerouslySetInnerHTML={{
              __html: selectedFeature.description
            }}>
            </Typography>

          </div>
        </div>
      </div>
      <MyButton
        variant="primary"
        text={t('features.liveDemoButton')}
        css={LiveDemoButtonStyle}
        onClick={openLiveDemo}
        endIcon={<LaunchIcon />}
      />
    </div>
  );
};
