const TitleAndDescription = () => {
    const { t } = useTranslation();

    return <>
        <Typography css={ContactTitleStyle}>
            {t('contact.pageTitle')}
        </Typography>

        <div css={ContactDescriptionContainerStyle}>
            <Typography css={ContactDescriptionStyle}
                dangerouslySetInnerHTML={{
                    __html: t('contact.detailedDescription').replace(
                        /<accent>(.*?)<\/accent>/g,
                        (_: any, text: any) => `<span style="color: #C45526; font-weight: 600;">${text}</span>`
                    )
                }}
            />
        </div>
    </>
}