/** @jsxImportSource @emotion/react */
import { css, keyframes } from '@emotion/react';
import { useState } from 'react';
import { withMyTheme } from "../theme/theme";
import { Menu as MenuIcon, Close as CloseIcon } from '@mui/icons-material';
import { IconButton } from '@mui/material';
import { SocialMediaIcons } from '../landing/footer/SocialMediaIcons';
import { HOW_TO_ID } from '../landing/howTo/HowTo';
import { useTranslation } from 'react-i18next';
import { NavItems } from './Navigation';
import { LanguageSelector } from '../components/LanguageSelector';

const MenuButtonStyle = withMyTheme((theme) => css`
    position: fixed;
    top: 20px;
    right: 5vw;
    z-index: 1000;
    color: ${theme.palette.common.white};
    
    /* Style the ripple effect using theme colors */
    .MuiTouchRipple-child {
        background-color: ${theme.palette.primary.main};
    }
`);

const fadeIn = keyframes`
    from { opacity: 0; }
    to { opacity: 1; }
`;

const slideIn = keyframes`
    from { transform: translateX(100%); }
    to { transform: translateX(0); }
`;

const OverlayStyle = withMyTheme((theme) => css`
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 100;
    display: flex;
    justify-content: center;
    align-items: center;
    animation: ${fadeIn} 0.3s ease-out;
`);

const MenuContainerStyle = withMyTheme((theme) => css`
    display: flex;
    flex-direction: column;
    gap: 2vh;
    text-align: center;
    animation: ${slideIn} 0.3s ease-out;
`);

const MenuItemStyle = withMyTheme((theme) => css`
    color: ${theme.palette.common.white};
    text-decoration: none;
    font-family: ${theme.typography.body1.fontFamily};
    font-size: 24px;
    font-weight: 400;
    cursor: pointer;
    transition: opacity 0.3s;
    padding: 10px;

    &:hover {
        opacity: 0.8;
    }
`);

const SocialMediaIconsStyle = withMyTheme(() => css`
    margin-top: 2vh;
`);

const LanguageSelectorContainerStyle = withMyTheme(() => css`
    margin-bottom: 1vh;
    display: flex;
    justify-content: center;
`);

export const NavigationMobile = () => {
    const [isMenuOpen, setIsMenuOpen] = useState(false);

    const { t } = useTranslation();

    const scrollToSection = (id: string) => {
        const element = document.getElementById(id);
        if (element) {
            element.scrollIntoView({ behavior: 'smooth' });
            setIsMenuOpen(false);
        }
    };

    return (
        <>
            <IconButton 
                css={MenuButtonStyle}
                onClick={() => setIsMenuOpen(!isMenuOpen)}
                aria-label={isMenuOpen ? "Close menu" : "Open menu"}
                disableRipple={false}
                centerRipple={true}
                touchRippleRef={undefined}
            >
                {isMenuOpen ? <CloseIcon fontSize="large" /> : <MenuIcon fontSize="large" />}
            </IconButton>

            {isMenuOpen && (
                <div css={OverlayStyle}>
                    <nav css={MenuContainerStyle}>
                        {NavItems.map((item) => (
                            <span key={item.id} css={MenuItemStyle} onClick={() => scrollToSection(item.id)}>{item.getText(t)}</span>
                        ))}
                        <div css={LanguageSelectorContainerStyle}>
                            <LanguageSelector />
                        </div>
                        <div css={SocialMediaIconsStyle}>
                            <SocialMediaIcons negative={true}/>
                        </div>
                    </nav>
                </div>
            )}
        </>
    );
};
